import * as React from "react";
import { createRoot } from "react-dom/client";
import App from "./components/App";
import { FluentProvider, webLightTheme, webDarkTheme} from "@fluentui/react-components";

/* global document, Office, module, require */

const title = "Richards Law";

const rootElement = document.getElementById("container");
const root = rootElement ? createRoot(rootElement) : undefined;

function getSystemTheme() {
  return window.matchMedia("(prefers-color-scheme: dark)").matches
    ? "dark"
    : "light";
}

/* Render application after Office initializes */
Office.onReady(() => {
  if (Office.context.mailbox) {
    Office.context.mailbox.addHandlerAsync(
      Office.EventType.ItemChanged,
      () => {
        // Dispatch a DOM event React can listen to
        window.dispatchEvent(new Event("emailChanged"));
      }
    );
  }
  
  function renderApp() {
    root?.render(
    <FluentProvider theme={getSystemTheme() === "dark" ? webDarkTheme : webLightTheme}>
      <App title={title} />
    </FluentProvider>
  );
  }
  
  renderApp();

  // Listen for OS theme changes
  const media = window.matchMedia("(prefers-color-scheme: dark)");
  media.addEventListener("change", renderApp);
});

if (module.hot) {
  module.hot.accept("./components/App", () => {
    const NextApp = require("./components/App").default;
    root?.render(NextApp);
  });
}
