import React, { useState } from 'react';

// --- Header Component (Adapted for compatibility) ---
const Header = ({ title, logo, message }) => {
  // Styles translated from the Fluent UI "makeStyles" request
  const headerStyle = {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    paddingTop: "20px", // Adjusted slightly for balance
    backgroundColor: "transparent", // Neutral background
    width: "100%",
  };

  const messageStyle = {
    fontSize: "42px", // equivalent to fontSizeHero900
    fontWeight: "400", // equivalent to fontWeightRegular
    // color: "#242424", // equivalent to colorNeutralBackgroundStatic
    // margin: "1px 0 0 0",
    textAlign: "center",
    fontFamily: "'Segoe UI', sans-serif",
  };

  return (
    <section style={headerStyle}>
      {/* Fallback added in case the specific image file isn't found in the preview environment */}
      <img 
        src={logo} 
        alt={title} 
        style={{ width: "90px", height: "auto" }}
        onError={(e) => {
          e.target.onerror = null; 
          e.target.style.display = 'none';
          e.target.parentNode.innerHTML += '<span style="font-size: 50px;"></span>'; // Fallback icon
        }}
      />
      <h1 style={messageStyle}>{message}</h1>
    </section>
  );
};

export default Header;